.mainNav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-flow: wrap;
  height: 100%;
  width: 70%;
}

.page {
  height: 100px;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  border: 1px solid rgba(193, 140, 93, 0.8);
  border-radius: 5%;
  font-size: 10px;
  text-decoration: none;
  background-color: rgba(37, 36, 34, 0.8);
  color: white;
}
